import React from "react"

export const Header = ({ handleSearch, search }) => {
  return (
    <div className="Header">
      <h1>ПОИСК РЕЙСА</h1>
      <input onChange={(event) => handleSearch(event.target.value)} value={search} placeholder="  Город"></input>
      <button>НАЙТИ</button>
    </div>
  )
}