import React from 'react';
import './App.css';
import "react-datepicker/dist/react-datepicker.css";

import { Nav } from "./Components/Nav" 
import { Header } from "./Components/Header" 

class App extends React.Component {
  generateDateTOYYMMDD = (date, number) => {
    var DateDay = date.getDate();
    DateDay = (DateDay<10) ? "0"+DateDay : DateDay;
    var DateMonth = date.getMonth() + 1;
    DateMonth = (DateMonth<10) ? "0"+DateMonth : DateMonth;
    var DateYear = date.getFullYear();
    var DateStr =  DateDay + "-" + DateMonth+ "-" + DateYear;
    return DateStr;
  }

  state = {
    search: '',
    currentDate: this.generateDateTOYYMMDD(new Date()),
    data: {},
    isvisible: false,
    direction: ['departure', 'airportToID.city_ru', 'timeLandFact'],
    activeButton: true
  }

  getData = (data) => {
    this.setState({ isvisible: false, })
    fetch(`https://api.iev.aero/api/flights/${data}`)
    .then(res => res.json())
    .then(data => this.setState({ data, isvisible: true, }))
  }
  componentDidMount() {
    this.getData(this.state.currentDate)
  }

  handleDirection = (direction) => {
    if (direction === 'out') {
      this.setState({ direction: ['departure', 'airportToID.city_ru', 'timeLandFact'], activeButton: true })
    }
    if (direction === 'in') {
      this.setState({ direction: ['arrival', 'airportFromID.city_ru', 'timeDepFact'], activeButton: false })
    }
  }
  handleSearch = (value) => {
    this.setState({ search: value })
  }
  handlesetDate = (value) => {
    let dat = new Date()
    if (value === "-1") {
      let corectDate = new Date(dat.getFullYear(), dat.getMonth(), dat.getDate() - 1)
      value = this.generateDateTOYYMMDD(corectDate).split('-').reverse().join('-');;
    }
    if (value === "+1") {
      let corectDate = new Date(dat.getFullYear(), dat.getMonth(), dat.getDate() + 1)
      value = this.generateDateTOYYMMDD(corectDate).split('-').reverse().join('-');;
    }
    if (value === "0") {
      let corectDate = new Date(dat.getFullYear(), dat.getMonth(), dat.getDate())
      value = this.generateDateTOYYMMDD(corectDate).split('-').reverse().join('-');;
    }
    console.log(value)
    let dateReverse = value.split('-').reverse().join('-');
    this.setState({ currentDate: dateReverse})
    this.getData(dateReverse)
    console.log(value)
    
    
  }
  render() {
    return (
     
      <div className="App">
        <Nav />
        <Header search={this.state.search} handleSearch={this.handleSearch}/>
        <HeaderTable 
          currentDate={this.state.currentDate}
          search={this.state.search}
          direction={this.state.direction} 
          isvisible={this.state.isvisible} 
          data={this.state.data}
          handleDirection={this.handleDirection}
          activeButton={this.state.activeButton}
          setDate={this.handlesetDate}
        />  
      </div>
    );
  }
}

const Table = ({ data, isvisible, direction, search }) => {
  if(!isvisible) return (<div>loading...</div>)
    
  let arr = data.body[direction[0]].filter(el => 
    (~el[direction[1]].toLowerCase().indexOf(search.toLowerCase())) ? true : false
   ) 
  console.log(arr)
  
  
  return (
    <table className="table">
      <tbody >
        <tr>
          <th>Терминал</th>
          <th>Время</th>
          <th>Место назначения</th>
          <th>Статус</th>
          <th>Авиакомпания</th>
          <th>Рейс</th>
          <th>---</th>
        </tr>
        {arr.map((el, i) => 
          <tr className={i%2 === 0 ? 'white' : ''} key={i}>
            <td>{el.term}</td>
            <td>{el[direction[2]] === undefined ? '' : el[direction[2]].slice(11, -4)}</td>
            <td>{el[direction[1]]}</td>
            <td>{el.status === 'DP' 
              ? `Вылетел в ${el.timeDepShedule.slice(11, -4) }` 
              : el.status === 'LN' 
              ? 'Прибыл' 
              : el.status === 'CX' ? 'Отменен' : ''}</td>
            <td>{el.codeShareData.map((el, i) => <span key={i}>{el.airline.ru.name}<br /></span>)}</td>
            <td>{el.codeShareData[0].codeShare}</td>
            <td>---</td>
          </tr>
        )}
      </tbody>
    </table>
  )
}

const HeaderTable = ({ data, isvisible, direction, handleDirection, activeButton, search, currentDate, setDate }) => {
  let dateReverse = currentDate.split('-').reverse().join('-');
  return (
    <div className="HeaderTable">
      <div className="buttonDirect">
        {activeButton === true 
          ? <><button className="buttonout">ВЫЛЕТ</button>
            <button onClick={() => handleDirection('in')} className="buttonin">ПРИЛЕТ</button></>
          : <><button onClick={() => handleDirection('out')} className="buttonin">ВЫЛЕТ</button>
            <button className="buttonout">ПРИЛЕТ</button></>
        }
        
      </div>
      <menu>
        <div className="calendar">
          <label>
            <input onChange={(event) => setDate(event.target.value)} type="date" value={dateReverse}></input>
          </label>
        </div>
        <div onClick={() => setDate('-1')} className="SelectDay">{'Вчера'}</div>
        <div onClick={() => setDate('0')} className="SelectDay">{'Сегодня'}</div>
        <div onClick={() => setDate('+1')} className="SelectDay">{'Завтра'}</div>
      </menu>
      <Table 
        data={ data } 
        isvisible={isvisible} 
        direction={direction}
        search={search}
      />
    </div>
  )
}



export default App;
